<template>
    <div id="3d_modeling">
        <section class="title-hero-bg portfolio-cover-bg pb-80" data-stellar-background-ratio="0.2">
            <div class="container">
                <div class="page-title text-center">
                    <h1>3D Modeling</h1>
                </div>
            </div>
        </section>
        <section class="pt-30">
            <div class="container">
                <div class="row">
                    <div class="portfolio-container text-center">
                        <ul id="portfolio-grid" class="two-column hover-two">
                            <li class="portfolio-item gutter-space" v-for="model in models" :key="model.name">
                                <div class="portfolio">
                                    <!--                                    <div class="dark-overlay"></div>-->
                                    <img :src="model.image.path" alt/>
                                    <div class="font-16px text-left col-md-10 pl-0 pt-10">
                                        <p class="font-400 remove-margin"
                                           v-for="text in model.description"
                                           :key="text"> {{ text }}
                                        </p>
                                    </div>
                                    <div class="text-right col-md-2 pt-10">
                                        <a class='font-20px alpha-lightbox'
                                           style="margin-right: 10px; color: #1b1d1c;" :href="model.image.path"
                                           @click="showImage()">
                                            <i class="fa fa-image"></i>
                                        </a>
                                        <a v-if="model.video.length > 10" class='font-20px' style="color: #1b1d1c" href="#"
                                           @click="showModel(model.video)">
                                            <i class="fa fa-cube"></i>
                                        </a>
                                    </div>
                                    <!--                                    <div class="portfolio-wrap">-->
                                    <!--                                        <div class="portfolio-description">-->
                                    <!--                                            <h3 class="portfolio-title">{{ model.name }}</h3>-->
                                    <!--                                        </div>-->

                                    <!--                                    </div>-->
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>

import ThreeDimModel from "./partials/ThreeDimModel";
import JQuery from "jquery";

export default {
    name: "3D Animation and Modeling",
    data: function () {
        return {
            models: [
                {
                    name: "Steam Punk Boat",
                    description: ['Steam Punk Boat (2020)'],
                    video: 'assets/images/portfolio/3d/FlyingBoat.mp4',
                    image: {
                        path: 'assets/images/portfolio/3d/FlyingBoat.png',
                        height: 480,
                        width: 720,
                    }
                },
                {
                    name: "Elephant",
                    description: ['Elephant (2020)'],
                    video: 'assets/images/portfolio/3d/Elephant.mp4',
                    image: {
                        path: 'assets/images/portfolio/3d/Elephant.png',
                        height: 480,
                        width: 720,
                    }
                },
                {
                    name: "Dean Winchester",
                    description: ['Dean Winchester (2020)'],
                    video: '#',
                    image: {
                        path: 'assets/images/portfolio/3d/DeanWinchester.png',
                        height: 480,
                        width: 720,
                    }
                },
                {
                    name: "Halloween Charmander",
                    description: ['Halloween Charmander (2020)'],
                    video: 'assets/images/portfolio/3d/Charmander.mp4',
                    image: {
                        path: 'assets/images/portfolio/3d/Charmander.png',
                        height: 480,
                        width: 720,
                    }
                },
                {
                    name: "Hand",
                    description: ['Hand (2020)'],
                    video: 'assets/images/portfolio/3d/Hand.mp4',
                    image: {
                        path: 'assets/images/portfolio/3d/Hand.png',
                        height: 480,
                        width: 720,
                    }
                }
            ]
        };
    },
    methods: {
        showImage: function () {
            let $ = JQuery;
            $('.alpha-lightbox').magnificPopup({
                type: 'image',
                mainClass: 'mfp-fade',
                removalDelay: 160,
                fixedContentPos: false
                // other options
            });
        },
        showModel: function (modelPath) {
            this.$modal.show(
                ThreeDimModel,
                {
                    modelPath: modelPath
                },
                {
                    adaptive: true,
                    height: 'auto',
                    width: '60%',
                }
            );
        }
    }
};
</script>

<style>
@media (min-width: 1300px) {
    .container {
        width: 1200px
    }
}
</style>