<template>
    <div id="home">
        <section class="pt-0 pb-0" style="background-color: #000000">
            <div class="hero-text-wrap overlay-bg">

                <div>
                    <div style="padding:56.25% 0 0 0;">
                        <iframe id="vimeo_reel" src="https://player.vimeo.com/video/468855250?title=0&byline=0&portrait=0"
                                style="position:absolute;top:0;left:0;width:100%;height:100%;" frameborder="0"
                                allow="autoplay;fullscreen;" allowfullscreen></iframe>
                    </div>
                </div>

                <div class="hero-text white-color" style="position:unset">
                </div>

            </div>
            <div class="homepage-hero-module">
                <div class="video-container">
                    <div class="filter"></div>
                    <div class="poster hidden">
                        <img src="assets/images/video/alpha-video.jpg" alt="video-img"/>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import JQuery from "jquery";

export default {
    name: "Reel",
    data: function () {
        return {
            name: "",
            showReel: false,
        };
    },
    mounted() {
        this.scaleVideo();
    },
    methods: {
        hideReel: function() {
            this.showReel = false;
        },
        scaleVideo: function () {
            let $ = JQuery;
            scaleVideoContainer();

            initBannerVideoSize(".video-container .poster img");
            initBannerVideoSize(".video-container .filter");
            initBannerVideoSize(".video-container video");

            $(window).on("resize", function () {
                scaleVideoContainer();
                scaleBannerVideoSize(".video-container .poster img");
                scaleBannerVideoSize(".video-container .filter");
                scaleBannerVideoSize(".video-container video");
            });

            function scaleVideoContainer() {
                var height = $(window).height() + 5;
                var unitHeight = parseInt(height, 10) + "px";
                $(".homepage-hero-module").css("height", unitHeight);
            }

            function initBannerVideoSize(element) {
                $(element).each(function () {
                    $(this).data("height", $(this).height());
                    $(this).data("width", $(this).width());
                });

                scaleBannerVideoSize(element);
            }

            function scaleBannerVideoSize(element) {
                var windowWidth = $(window).width(),
                    windowHeight = $(window).height() + 5,
                    videoWidth,
                    videoHeight;

                $(element).each(function () {
                    var videoAspectRatio = $(this).data("height") / $(this).data("width");

                    $(this).width(windowWidth);

                    if (windowWidth < 1000) {
                        videoHeight = windowHeight;
                        videoWidth = videoHeight / videoAspectRatio;
                        $(this).css({
                            "margin-top": 0,
                            "margin-left": -(videoWidth - windowWidth) / 2 + "px"
                        });

                        $(this)
                            .width(videoWidth)
                            .height(videoHeight);
                    }

                    $(".homepage-hero-module .video-container video").addClass(
                        "fadeIn animated"
                    );
                });
            }
        }
    }
};
</script>

