import Vue from 'vue';
import Router from 'vue-router';

//Components
import Home from './components/Home.vue';
import Illustration from './components/Illustration.vue';
import Animation from './components/Animation.vue';
import ThreeDim from './components/ThreeDim.vue';
import PersonalWorks from './components/PersonalWorks.vue';
import About from './components/About.vue';
import Reel from "@/components/Reel";

Vue.use(Router);

const router = new Router({
    mode: 'hash',
    routes: [
        {
            path: '/',
            component: Home,
            name: "Home"
        },
        {
            path: '/illustration',
            component: Illustration,
            name: "Illustration"
        },
        {
            path: '/animation',
            component: Animation,
            name: "Animation"
        },
        {
            path: '/3d',
            component: ThreeDim,
            name: "3D"
        },
        {
            path: '/personal-works',
            component: PersonalWorks,
            name: "Personal Works"
        },
        {
            path: '/about',
            component: About,
            name: "About"
        },
        {
            path: '/reel',
            component: Reel,
            name: "Reel"
        },
    ]
});

export default router;
